import React from 'react';
import './Hero.css';

class Hero extends React.Component {
    openProjects = () =>{
        this.props.settingProjectsState(true);
    }
    render() {
        return (
        <div className='hero'>
            <div className='heroLayer'></div>
           <a onClick={this.openProjects} href="#" className="btn btn-white btn-animated">My Projects</a>
        </div>);
    }
}

export default Hero;