import React from 'react';
import './MyProjects.css';
import gradient from './gradient.png';
import robots from './robots.png';
import facerecognition from './facerecognition.png';

class MyProjects extends React.Component {
    closeProjects = () =>{
        this.props.settingProjectsState(false);
    }

    render () {
        return (
        <div className='projectsBlock'>
            <div id='he' className='projectsSlides'>
            <h2>My Projects On GitHub:</h2>
            <i class="fas fa-caret-left"></i>
            <div onMouseEnter={this.addLinkIcon} className='imgFrame'>
            <a href='https://github.com/Sloor/backgroundGenerator/tree/master/gradient' target='_blank' rel="noopener noreferrer">
            <img className='first' src={gradient} alt='gradientProj'/>
                <div className='textover'>
                <p>A website for trying different color gradients for designing purposes</p>
                <i className="fas fa-external-link-alt"></i>
                </div>
            </a>
            </div>
            <div className='imgFrame'>
            <a href='https://sloor.github.io/robotsearch/' target='_blank' rel="noopener noreferrer"><img className='second' src={robots} alt='robotsProj'/>
                <div className='textover'>
                <p>A simple page demonstrating searching using AJAX</p>
                <i className="fas fa-external-link-alt"></i>
                </div>
            </a>
            </div>
            <div className='imgFrame'>
            <a href='https://sloor.github.io/facerecognition/' target='_blank' rel="noopener noreferrer"><img className='third' src={facerecognition} alt='todoProj'/>
                <div className='textover'>
                <p>A face recognition applicaiton that accepts a URL input</p>
                <i className="fas fa-external-link-alt"></i>
                </div>
            </a>
            </div>
            <i class="fas fa-caret-right"></i>
            </div>
            <div onClick={this.closeProjects} className='projectsLayer'></div>
        </div>
        );   
    }
}

export default MyProjects;