import React, { Component } from 'react';
import Hero from './components/Hero/Hero';
import SideBar from './components/SideBar/SideBar';
import About from './components/About/About';
import MyProjects from './components/MyProjects/MyProjects';
import './App.css';

class App extends Component {
  constructor() {
    super();
    this.state = {
      projects:false,
    }
    this.settingProjectsState = this.settingProjectsState.bind(this);
  }
  settingProjectsState(value) {
    this.setState({projects:value});
  }
  render() {
    return (
      <div className='App'>
        <Hero settingProjectsState={this.settingProjectsState}/>
        <SideBar />
        <About />
        {
          (this.state.projects)?
          <MyProjects settingProjectsState={this.settingProjectsState}/>
          :''
        }
        
      </div>
    );
  }
}

export default App;
