import React from 'react';
import './SideBar.css';
import me from './me.jpg';

const SideBar = () => {
    return (
    <div className='sidebar'>
        <img className='me' alt='me' src={me}/>
        <div className='layer'></div>
        <span><i className="quotes fas fa-quote-left"></i></span><p className='quote'>Growth is painful    <br/>
           Change is painful<br/>
           But nothing is more painful than staying stuck somewhere you don't belong</p><span><i className="quotes fas fa-quote-right"></i></span>
        <div className='socials'>
            <a href="https://www.facebook.com/aseel.mohammad.71" target="_blank" rel="noopener noreferrer"><i className="fab fa-facebook-square"></i></a>
		    <a href="https://www.linkedin.com/in/aseel-abu-sal-893a31a7/" target="_blank" rel="noopener noreferrer"><i className="fab fa-linkedin"></i></a>
		    <a href="https://twitter.com/AbusalAseel" target="_blank" rel="noopener noreferrer"><i className="fab fa-twitter-square"></i></a>
            <a href="https://github.com/Sloor/" target="_blank" rel="noopener noreferrer"><i className="fab fa-github-square"></i></a>
        </div>
    </div>
    );
}

export default SideBar;