import React from 'react';
import './About.css';

class SideBar extends React.Component {
    constructor() {
        super();
        this.aboutMeExtend = this.aboutMeExtend.bind(this);
        this.aboutMeShrink = this.aboutMeShrink.bind(this);
        this.begin = this.begin.bind(this);
    }
    aboutMeExtend() {
        const aboutElement = document.getElementsByClassName('aboutMe')[0];
        const infoElement = document.getElementsByClassName('info')[0];
        aboutElement.style.cssText= 'display:block;width:300px;animation: extend 1s;';
        setTimeout(() => {
            infoElement.style.cssText= 'display: block;animation: fadeIn 1s;'
        }, 1000);
    }
    aboutMeShrink(e) {
        e.preventDefault();
        const aboutElement = document.getElementsByClassName('aboutMe')[0];
        const infoElement = document.getElementsByClassName('info')[0];
        infoElement.style.cssText= 'display: none;animation: fadeOut 1s;'
        setTimeout(() => {
            aboutElement.style.cssText= 'display:block;width: 100px;animation: shrink 1s;';
        }, 500);
    }
    
    begin() {
        document.addEventListener("DOMContentLoaded", ()=>{
        const aboutElement = document.getElementsByClassName('aboutMe')[0];
        setTimeout(() => {
            aboutElement.style.cssText= 'display: block;animation: fadeIn 1s;'
        }, 3000);
        })
    }

    render() {
        return (
        <div begin={this.begin()} onMouseEnter={this.aboutMeExtend} onMouseLeave={this.aboutMeShrink} className='aboutMe'>
            <p>About Me</p>
        <div className='info'><p>Hi, my name is Aseel Abusal</p>
            <p>My Email: aseel.abosul@gmail.com</p></div>
        </div>
        );
    }
}

export default SideBar;